/**
 * @description: 
 * @param {CanvasContext} ctx canvas
 * @param {number} x  x
 * @param {number} y  y
 * @param {number} w 
 * @param {number} h 
 * @param {number} r 
 * @param {String} url url
 */
export function drawSquarePic(ctx, x, y, w, h, r, url) {
  ctx.save()
  ctx.beginPath()
  // 
  ctx.arc(x + r, y + r, r, Math.PI, Math.PI * 1.5)
  // border-top
  //  xy
  ctx.lineTo(x + w - r, y)
  // 
  ctx.arc(x + w - r, y + r, r, Math.PI * 1.5, Math.PI * 2)
  // border-right
  ctx.lineTo(x + w, y + h - r)
  // 
  ctx.arc(x + w - r, y + h - r, r, 0, Math.PI * 0.5)
  // 
  ctx.arc(x + r, y + h - r, r, Math.PI * 0.5, Math.PI)
  // border-left
  ctx.lineTo(x, y + r)
  // ()
  ctx.setFillStyle('#ffffff')
  ctx.fill()
  // saverestore  
  ctx.clip()

  // 
  return new Promise((resolve, reject) => {
    if (url) {
      wx.getImageInfo({
        src: url,
        success(res) {
          ctx.drawImage(res.path, x, y, w, h)
          ctx.restore() //canvas
          ctx.draw(true)
          resolve()
        },
        fail(res) {
          console.log('fail -> res', res)
          uni.showToast({
            title: '',
            duration: 2000,
            icon: 'none'
          })
        }
      })
    } else {
      ctx.draw(true)
      resolve()
    }
  })
}

/**
 * @description: 
 * @param {type}
 * @return {type}
 * @author: hch
 */
export function getSystem() {
  let system = wx.getSystemInfoSync()
  let scale = system.windowWidth / 375 // 375*667  
  return { w: system.windowWidth, h: system.windowHeight, scale: scale }
}

/**
 * @description: 
 * @param {Object} ctx canvas
 * @param {String} text 
 * @param {Number} x X
 * @param {Number} y Y
 * @param {Number} maxWidth 
 * @param {Number} fontSize 
 * @param {String} color 
 * @param {Number} lineHeight 
 * @param {String} textAlign 
 */
export function drawTextReturnH(
  ctx,
  text,
  x,
  y,
  maxWidth = 375,
  fontSize = 14,
  color = '#000',
  lineHeight = 30,
  textAlign = 'left'
) {
  if (textAlign) {
    ctx.setTextAlign(textAlign) //  ctx.setTextAlign ctx.textAlign
    switch (textAlign) {
      case 'center':
        x = getSystem().w / 2
        break

      case 'right':
        x = (getSystem().w - maxWidth) / 2 + maxWidth
        break

      default:
        // 
        x = (getSystem().w - maxWidth) / 2
        break
    }
  }
  let arrText = text.split('')
  let line = ''
  for (let n = 0; n < arrText.length; n++) {
    let testLine = line + arrText[n]
    ctx.font = fontSize + 'px sans-serif' // ctx.setFontSize
    ctx.setFillStyle(color) //
    let metrics = ctx.measureText(testLine) //measureText() 
    let testWidth = metrics.width
    if (testWidth > maxWidth && n > 0) {
      ctx.fillText(line, x, y)
      line = arrText[n]
      y += lineHeight
    } else {
      line = testLine
    }
  }
  ctx.fillText(line, x, y)
  ctx.draw(true) // reserve  false native  reserve  true drawCanvas  false
  return y
}
