import uniStarterConfig from '@/uni-starter.config.js';
import store from '@/store'
//





const db = uniCloud.database()
export default async function() {
	let loginConfig = uniStarterConfig.router.login
	//






























	//apph5

	loginConfig = loginConfig.filter(item => {
		return ![
			'univerify',
			'apple',



		].includes(item)
	})


	uniStarterConfig.router.login = loginConfig

	// uniStarterConfiggetApp().globalData.config
	setTimeout(() => {
		getApp({
			allowDefault: true
		}).globalData.config = uniStarterConfig;
	}, 1)


	// appVersionapp
	initAppVersion();







	//clientDB
	function onDBError({
		code, // https://uniapp.dcloud.net.cn/uniCloud/clientdb?id=returnvalue
		message
	}) {
		console.log('onDBError', {
			code,
			message
		});
		// 
		console.error(code, message);
		if ([
				'TOKEN_INVALID_INVALID_CLIENTID',
				'TOKEN_INVALID',
				'TOKEN_INVALID_TOKEN_EXPIRED',
				'TOKEN_INVALID_WRONG_TOKEN',
				'TOKEN_INVALID_ANONYMOUS_USER',

			].includes(code)) {
			uni.navigateTo({
				url: '/pages/ucenter/login-page/index/index'
			})
		}
	}
	// clientDB
	db.on('error', onDBError)

	// clientDB
	//db.off('error', onDBError)
	db.on('refreshToken', function({
		token,
		tokenExpired
	}) {
		console.log('clientDBrefreshToken', {
			token,
			tokenExpired
		});
		store.commit('user/login', {
			token,
			tokenExpired
		})
	})

	const Debug = false;
	//callFunction
	let callFunctionOption;
	uniCloud.addInterceptor('callFunction', {
		async invoke(option) {

























































































			// console.log(JSON.stringify(option));
			callFunctionOption = option
		},
		complete(e) {
			// console.log(JSON.stringify(e));
		},
		fail(e) { // 
			if (Debug) {
				uni.showModal({
					content: JSON.stringify(e),
					showCancel: false
				});
				console.error(e);
			} else {
				uni.showModal({
					content: "",
					showCancel: false,
					confirmText: ""
				});
			}
			//
			uni.getNetworkType({
				complete: res => {
					// console.log(res);
					if (res.networkType == 'none') {
						uni.showToast({
							title: '',
							icon: 'none'
						});
						console.log('');
						let callBack = res => {
							console.log(res);
							if (res.isConnected) {
								uni.showToast({
									title: '',
									icon: 'none'
								});
								console.log(res.networkType, "");
								uni.offNetworkStatusChange(e => {
									console.log("", e);
								})
								//
								uniCloud.callFunction(callFunctionOption)
								uni.offNetworkStatusChange(callBack);
							}
						}
						uni.onNetworkStatusChange(callBack);
					}
				}
			});
		},
		success: (e) => {
			// console.log(e);
			const {
				token,
				tokenExpired
			} = e.result
			if (token && tokenExpired) {
				store.commit('user/login', {
					token,
					tokenExpired
				})
			}
			switch (e.result.code) {
				case 403:
					uni.navigateTo({
						url: "/pages/ucenter/login-page/index/index"
					})
					break;
				case 30203:
					uni.navigateTo({
						url: "/pages/ucenter/login-page/index/index"
					})
					break;
				case 50101:
					uni.showToast({
						title: e.result.msg,
						icon: 'none',
						duration: 2000
					});
					break;
				default:
					console.log('code' + e.result.code, 'case');
					break;
			}

			switch (e.result.errCode) {
				case 'uni-id-token-not-exist':
					uni.showToast({
						title: '',
						icon: 'none'
					});
					uni.navigateTo({
						url: "/pages/ucenter/login-page/index/index"
					})
					break;
				default:
					break;
			}
		}
	})

	//
	const {
		"router": {
			needLogin,
			visitor,
			login
		}
	} = uniStarterConfig //

	let list = ["navigateTo", "redirectTo", "reLaunch", "switchTab"];
	list.forEach(item => { //,uni.navigateTo,uni.redirectTo,uni.reLaunch,uni.switchTab4
		uni.addInterceptor(item, {
			invoke(e) { // 
				//token
				const token = uni.getStorageSync('uni_id_token'),
					//token
					tokenExpired = uni.getStorageSync('uni_id_token_expired') < Date.now(),
					//url"?""?"
					url = e.url.split('?')[0];
				//url"?""?"
				const pages = getCurrentPages();
				if (!pages.length) {
					console.log("");
					return e
				}
				const fromUrl = pages[pages.length - 1].route;

				let inLoginPage = fromUrl.split('/')[2] == 'login-page'

				//
				if ( //
					url == '/pages/ucenter/login-page/index/index' &&
					!inLoginPage
				) {
					//univerifyusernamesmsCode
					if (login[0] == 'username') {
						e.url = "/pages/ucenter/login-page/pwd-login/pwd-login"
					} else {
						if (e.url == url) {
							e.url += '?'
						} //````
						e.url += "type=" + login[0]
					}
				} else {
					//
					let pass = true
					//pattern
					if (needLogin) {
						pass = needLogin.every((item) => {
							if (typeof(item) == 'object' && item.pattern) {
								return !item.pattern.test(url)
							}
							return url != item
						})
						// console.log({pass})
					}
					if (visitor) {
						pass = visitor.some((item) => {
							if (typeof(item) == 'object' && item.pattern) {
								return item.pattern.test(url)
							}
							return url == item
						})
						// console.log({pass})
					}

					if (!pass && (token == '' || tokenExpired)) {
						uni.showToast({
							title: '',
							icon: 'none'
						})
						uni.navigateTo({
							url: "/pages/ucenter/login-page/index/index"
						})
						return false
					}
				}
				return e
			},
			fail(err) { //  
				console.log(err);
				if (Debug) {
					console.log(err);
					uni.showModal({
						content: JSON.stringify(err),
						showCancel: false
					});
				}
			}
		})
	})






















}
/**
 * // appVersion
 */
function initAppVersion() {

























}
